<script>
    export let title;
</script>

<style>
    header {
        max-width: 720px;
        margin-left: auto;
        margin-right: auto;
    }

    .title {
        padding: 0px 15px 15px;
        /* border-bottom: 2px solid #001f55; */
        position: relative;
    }

    h1 {
        font-size: 18px;
        font-weight: 700;
        line-height: 1.428;
        color: #001f55;
        bottom: 20px;
    }

    img {
        max-width: 100%;
        display: block;
        margin-top: 10px;
    }

    .flex {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
    }

    .flex > img {
        width: 50px;
    }

    .flex > img:nth-child(1) {
        transform: scaleX(-1) rotate(15deg);
    }

    .flex > img:nth-child(2) {
        transform: rotate(15deg);
    }

    h1,
    hr {
        position: absolute;
        left: 50%;
        transform: translateX(-50%);
    }

    hr {
        display: block;
        border: 0;
        border-top: 2px solid #001f55;
    }

    .title > hr:nth-child(3) {
        width: calc(100% - 130px);
        bottom: 10px;
    }

    .title > hr:nth-child(4) {
        width: calc(100% - 40px);
        bottom: 0;
    }

    @media (max-width: 342px) {
        h1 {
            font-size: 16px;
        }
    }

    @media (max-width: 303px) {
        h1 {
            font-size: 14px;
        }
    }

    @media (min-width: 400px) {
        h1 {
            font-size: 20px;
        }
    }

    @media (min-width: 500px) {
        h1 {
            font-size: 22px;
        }
    }

    @media (min-width: 700px) {
        h1 {
            font-size: 24px;
        }
    }
</style>

<header>
    <div class="title">
        <h1>{title}</h1>

        <div class="flex">
            <img src="img/icon.png" alt="" />
            <img src="img/icon.png" alt="" />
        </div>

        <hr />
        <hr />
    </div>
</header>
